#editor {
  height: 50%;
  width: 100%;
  font-size: 1.2em;
  font-family: 'Fira Code', monospace;
  line-height: 1.5em;
  background-color: #282c34;
  color: #ffffff;
  padding: 10px;
  box-sizing: border-box;
  border: none;
  outline: none;
  resize: none;

}
#editor:focus {
  outline: none;
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background-color: rgba(255, 255, 255, 0.4);
}

#preview{
  width: 100%;
  height: 50%;
  border: none;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
#preview iframe {
  width: 100%;
  height: 100%;
  border: none;

}
#preview:focus {
  outline: none;
}
#preview img {
  max-width: 100%;
  height: auto;
}
body, html, #root, .App {
  height: 100%;
  margin: 0;
}
.App {
  display: flex;
  flex-direction: column;
}
@media (max-width: 600px) {
  #editor, #preview {
    height: 50%;
  }
}



